.dnd-sample ul {
    display: inline-block;
    margin: 0;
    padding: 0;
    background-color: #eee;
  }
  
  .dnd-sample li {
    cursor: default;
    list-style: none;
    border-bottom: 1px solid #ddd;
    padding: 10px;
    margin: 0;
    width: 300px;
    background-color: #fff;
  }
  
  .dnd-sample-mask {
    position: fixed;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
    background: rgba(0, 0, 0, 0.1);
  }
  